<template>
  <div class="content-container">
    <Title name="属性选择器"/>
    <div class="table-display">


      <table>
        <thead>
        <tr>
          <th>编号</th>
          <th>角色名</th>
          <th>权限</th>
          <th>创建时间</th>
          <th>启动/停用</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td colspan="6">
            <a href="a">生活</a>
            <a href="#">知识</a>
            <a href="#1">开心</a>
            <a class="b" href="b1">开头</a>
            <a href="1b">结尾</a>
            <a href="b#">包含</a>
            <a href="#-12">开头本身或-</a>
          </td>
        </tr>
        </tbody>
      </table>

    </div>
  </div>
</template>

<script>
  import Title from '../../public/title'


    export default {
      components: {
        Title
      }
    }
</script>

<style lang="scss" scoped>
  .content-container {
    width: 100%;
    height: 710px;
    background-color: #fff;
    margin-top: 10px;
    overflow: hidden;
    .table-display {
      width: 1000px;
      height: 612px;
      border: 1px solid #ccc;
      margin: 0 0 0 36px;
      overflow: hidden;
      position: relative;
      .features {
        width: 100%;
        height: 60px;
        background-color: rgba(242,242,242,1);
        position: relative;
        button {
          position: absolute;
          top: 10px;
          right: 15px;
        }
      }
      table {
        width: 100%;

        thead {
          color: #666;
          tr {
            background-color: #f2f2f2;
            th {
              width: 17.7%;
              text-align: center;
              font-size: 14px;
              padding: 15px 0;
            }
          }
        }
        tbody {
          tr {
            td {
              font-size: 14px;
              a[href]{
                text-decoration: none;
                margin: 15px 5%;
              }
              a[href='#']{
                color: #000;
                font-weight: 400;
              }
              a[class~='b']{
                color: #00f;
              }
              a[href^='#']{
                color: #00ff00;
              }
              a[href$='#']{
                color: #ff0000;
              }

              a[href*='#']
              {
                color: #cf9236;
              }
              a[href|='#']{
                color: #a0cfff;
              }
            }
          }
        }
      }
    }

  }
</style>



