<template>
  <div class="content-container">
    <Title name="UI元素状态伪类"/>
    <div class="table-display">

      <div class="features">
        <el-input placeholder="按报告类型筛选" />
        <el-button type="primary" icon="el-icon-search">筛选</el-button>
        <el-button type="primary" icon="el-icon-cancel">取消</el-button>
      </div>


      <table>
        <thead>
        <tr>
          <th>编号</th>
          <th>角色名</th>
          <th>权限</th>
          <th>创建时间</th>
          <th>启动/停用</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td colspan="6">
            <input type="button" disabled="disabled"/>
            <input type="button"/>
            <input type="checkbox" checked="checked" class="a"/>


          </td>
        </tr>
        </tbody>
      </table>
      <form>
        <input checked="checked" id="radio_1" name="radio" type="radio">
        <label class="radio" for="radio_1">
          <i class="fa fa-times"></i>
        </label>
      </form>
    </div>
  </div>
</template>

<script>
  import Title from '../../public/title'


  export default {
    components: {
      Title
    }
  }
</script>



<style lang="scss" scoped>
  .content-container {
    width: 100%;
    height: 710px;
    background-color: #fff;
    margin-top: 10px;
    overflow: hidden;
    .table-display {
      width: 1000px;
      height: 612px;
      border: 1px solid #ccc;
      margin: 0 0 0 36px;
      overflow: hidden;
      position: relative;
      .features {
        width: 100%;
        height: 60px;
        background-color: rgba(242,242,242,1);
        position: relative;
        button {
          position: absolute;
        }

        button:first-of-type {
          top: 10px;
          left: 224px;
        }
        button:nth-of-type(2) {
          top: 10px;
          left: 325px;
        }
        .el-input {
          width: 200px;
          margin: 10px 10px;
        }
      }
      table {
        width: 100%;

        thead {
          color: #666;
          tr {
            background-color: #f2f2f2;
            th {
              width: 17.7%;
              text-align: center;
              font-size: 14px;
              padding: 15px 0;
            }
          }
        }
        tbody {
          tr {
            td {
              font-size: 14px;
              a:link{
                color: #ff0;
              }
              a:visited{
                color: #ff0000;
              }
              a:active{
                color: #000;
              }
              a:hover{
                color: #f0c78a;
              }
            }
          }
        }
        input{
          width:120px;
          height: 40px;
          padding: 10px;
          border: 1px solid #ccc;
        }
        input:disabled{
          border: 1px solid #00ff00;
        }
        input:focus{
          background: red;
        }
      }

        form
        {
          width: 100vw;
          height: 100vh;
          display: flex;
          flex-flow: column wrap;
          justify-content: center;
          align-items: center;
        }
        form .frame
        {
          display: flex;
          flex-flow: row nowrap;
        }
        form .frame input
        {
          display: none;
        }
        form .frame label
        {
          cursor: pointer;
          position: relative;
          width: 30px;
          height: 30px;
          margin: 10px;
          background: #8ABA56;
          transition: all 0.3s ease-in-out;
          font-size: 12pt;
          color: #FFF;
          -webkit-font-smoothing: antialiased;
        }
        form .frame label.radio
        {
          border-radius: 100%;
        }
        form .frame label.check
        {
          border-radius: 5px;
        }
        form .frame label .fa
        {
          position: absolute;
          top: 0;
          left: 0;
          rightright: 0;
          bottombottom: 0;
          opacity: 0;
          -webkit-transform: scale(0);
          transition: all 0.3s ease-in-out;
          line-height: 30px;
          text-align: center;
        }
        form .frame input:checked + label
        {
          background: #678b40;
        }
        form .frame input:checked + label .fa
        {
          opacity: 1;
          -webkit-transform: scale(1);
        }

    }

  }
</style>



