<template>
    <div class="content-container">
        <Title name="信用评分列表"/>
        <div class="table-display">
            <div class="choose">
                <span>选择企业</span>
                <select name="chooseEnter" id="chooseEnter">
                    <option value="test">
                        东大
                    </option>
                </select>
                <el-button type="primary" size="small">确定</el-button>

                <div class="table">
                    <MaterialTable :basicsituation="basicsituation" :basicname="basicname"/>
                </div>

                <div class="score">
                    <span>总得分：{{totleScore}}</span>    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Title from '../../../public/title.vue'
import MaterialTable from './materialTable.vue'
export default {
    data() {
        return {
            basicsituation: {},
            basicname: {},
            totleScore: 0
        }
    },
    components: {
        Title,
        MaterialTable
    },
    created() {
        this.getScore()
        // console.log(this.basicsituation)
    },
    methods: {
        getScore() {
            this.$http.get('score/scorecount.action?user_id=1&type=MaterialEquipmentManufacturing')
            .then( res => {
                this.basicsituation = res.data.responseCode.basicsituation
                this.basicname = res.data.responseCode.basicname
                // console.log(this.basicname)
            }).then( () => {
                let totleScore = 0
                for (const key in this.basicsituation) {
                    if (this.basicsituation.hasOwnProperty(key)) {
                        const element = this.basicsituation[key]
                        totleScore += element
                    }
                }
                this.totleScore = totleScore.toFixed(2)
                console.log(totleScore)
            } )
        },
    }
}

</script>

<style lang="scss" scoped>
.content-container {
    width: 100%;
    height: 710px;
    background-color: #fff;
    margin-top: 10px;
    overflow: hidden;
    .table-display {
        width: 1000px;
        height: 612px;
        border: 1px solid #ccc;
        margin: 0 0 0 36px;
        overflow: hidden;
        background-color: #f2f2f2;
        position: relative;
        .choose {
            margin: 15px 0 15px 15px;
            span {
                font-weight: 700;
                color: #666;
                margin-right: 10px;
            }
            select {
                min-width: 180px;
                height: 28px;
                font-size: 16px;
                border-radius: 4px;
                vertical-align: bottom;
                margin-right: 10px;
            }
            .table {
                width: 886px;
                height: 460px;
                background-color: #fff;
                margin: 20px 0 0 40px;
                padding-top: 10px;
                font-size: 13px;
                overflow: scroll;
               
            }
        }
    }
    .score {
        span {
            color: #f00 !important;
        }
        text-align: right;
        padding-right: 50px;
        padding-top: 20px;
    }
}
</style>




