<template>
  <div class="content-container">
    <Title name="动态伪类选择器"/>
    <div class="table-display">

      <div class="features baogao">
        <el-input placeholder="按报告类型筛选" />
        <el-button type="primary" icon="el-icon-search">筛选</el-button>
        <el-button type="primary" icon="el-icon-cancel">取消</el-button>
      </div>


      <table>
        <thead>
        <tr>
          <th>编号</th>
          <th>角色名</th>
          <th>权限</th>
          <th>创建时间</th>
          <th>启动/停用</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td colspan="6">

          </td>
        </tr>
        </tbody>
      </table>

    </div>
  </div>
</template>

<script>
  import Title from '../../public/title'


  export default {
    components: {
      Title
    }
  }
</script>



<style lang="scss" scoped>
  .content-container {
    width: 100%;
    height: 710px;
    background-color: #fff;
    margin-top: 10px;
    overflow: hidden;
    .table-display {
      width: 1000px;
      height: 612px;
      border: 1px solid #ccc;
      margin: 0 0 0 36px;
      overflow: hidden;
      position: relative;
      .features {
        width: 100%;
        height: 60px;
        background-color: rgba(242,242,242,1);
        position: relative;
        button {
          position: absolute;
        }

        button:first-of-type {
          top: 10px;
          left: 224px;
        }
        button:nth-of-type(2) {
          top: 10px;
          left: 325px;
        }
        .el-input {
          width: 200px;
          margin: 10px 10px;
        }
      }
      table {
        width: 100%;

        thead {
          color: #666;
          tr {
            background-color: #f2f2f2;
            th {
              width: 17.7%;
              text-align: center;
              font-size: 14px;
              padding: 15px 0;
            }
          }
        }
        tbody {
          tr {
            td {
              font-size: 14px;
              a:link{
                color: #ff0;
              }
              a:visited{
                color: #ff0000;
              }
              a:active{
                color: #000;
              }
              a:hover{
                color: #f0c78a;
              }
            }
          }
        }
        input{
          width:120px;
          padding: 10px;}
        input:focus{
          background: red;

        }
      }
    }

  }
</style>



