<template>
    <div class="content-container">
        <Title name="用户列表"/>
        <div class="table-display">
            <!-- 功能模块 -->
            <div class="features">
                <el-input placeholder="按报告类型筛选" />
                <el-button type="primary" icon="el-icon-search">筛选</el-button>
                <el-button type="primary" icon="el-icon-cancel">取消</el-button>
            </div>


            <table>
                <thead>
                    <tr>
                        <th>&nbsp;&nbsp;&nbsp;&nbsp;</th>
                        <th>编号</th>
                        <th>用户名</th>
                        <!-- <th>角色名</th> -->
                        <th>企业类型</th>
                        <th>企业名称</th>
                        <th>企业地址</th>
                        <th>联系人</th>
                        <th>电话</th>
                        <th>手机</th>
                        <th>邮箱</th>
                        <th>申请编号</th>
                        <th>状态</th>
                        <th>操作</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in userlist" :key="item.id">
                        <td><input type="checkbox"></td>
                        <td>{{item.id}}</td>
                        <td>{{item.userName}}</td>
                        <!-- <td>{{item.roleName}}</td> -->
                        <td>{{item.enterpriseType}}</td>
                        <td>{{item.enterpriseName}}</td>
                        <td>{{item.enterpriseAddress}}</td>
                        <td>{{item.linkman}}</td>
                        <td>{{item.telephone}}</td>
                        <td>{{item.mobilephone}}</td>
                        <td>{{item.email}}</td>
                        <td>{{item.applicationNumber}}</td>
                        <td>{{item.status}}</td>
                        <td>
                            <el-button type="primary" plain>审核</el-button>
                            <el-button type="warning" plain>修改</el-button>
                            <el-button type="danger" plain>删除</el-button>
                        </td>
                    </tr>
                </tbody>
            </table>

            <BottomFeature
                :pageNum='pageNum'
                :nextPage='nextPage'
                :pageSize='pageSize'
                :previousPage='previousPage'
                :display='true'/>
        </div>
    </div>
</template>

<script>
import Title from '../../../public/title.vue'
import BottomFeature from '../../../public/bottomFeature.vue'

export default {
    data() {
        return {
            userlist: [],
            multipleSelection: [],
            pageNum:0,
            pageSize:8,
            flag: true
        }
    },

    created() {
        this.getUserList()
    },

    methods: {
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },

    //获取用户列表
        getUserList() {
            this.$http.get(`http://192.168.3.33:8080/YinTouXY/user/showpage.action?pageNum=${this.pageNum}&pageSize=${this.pageSize}`)
            .then( res => {
                let userlist = res.data.responseCode.fac
                console.log(userlist)
                this.userlist = userlist
            } )
        },
    //下一页
        nextPage() {
            this.pageNum++
            this.getUserList()
        },
    //上一页
        previousPage() {
            this.pageNum--
            if(this.pageNum < 0) {
                this.pageNum = 0
            }
            this.getUserList()

        }
    },

    components: {
        Title,
        BottomFeature
    }
  }
</script>

<style lang="scss" scoped>
.content-container {
    width: 100%;
    height: 710px;
    background-color: #fff;
    margin-top: 10px;
    overflow: hidden;
    .table-display {
        width: 1000px;
        height: 612px;
        border: 1px solid #ccc;
        margin: 0 0 0 36px;
        overflow: hidden;
        background-color: #fff;
        position: relative;
        .features {
            width: 100%;
            height: 60px;
            background-color: rgba(242,242,242,1);
            position: relative;
            button {
                position: absolute;
            }
            button:first-of-type {
                top: 10px;
                left: 224px;
            }
            button:nth-of-type(2) {
                top: 10px;
                left: 325px;
            }
            .el-input {
                width: 200px;
                margin: 10px 10px;
            }
        }
        table {
            width: 100%;
            color: #666;
            thead {
                tr {
                    border-bottom: 1px solid #ddd;
                    border-top: 1px solid #ddd;
                    background-color: #f2f2f2;
                    th {
                        text-align: center;
                        font-size: 14px;
                        padding: 15px 0;
                    }
                }
            }
            tbody {
                tr {
                    border-bottom: 1px solid #ddd;
                    &:nth-child(even) {
                        background-color: #f2f2f2;
                    }
                    td {
                        text-align: center;
                        font-size: 14px;
                        padding: 15px 0;
                    }
                }
            }
        }
        .el-button--primary.is-plain,.el-button--danger.is-plain,.el-button--warning.is-plain {
            width: 35px;
            height: 20px;
            font-size: 12px;
            text-align: center;
            padding: 0;
            background-color: transparent;
        }
        .el-button--primary.is-plain {
            border:1px solid #2db7f5;
            &:hover {
                background-color: #2db7f5;
            }
        }
        .el-button--warning.is-plain {
            border:1px solid #ffcc33;
            &:hover {
                background-color: #ffcc33;
            }
        }
        .el-button--danger.is-plain {
            border:1px solid #ff3300;
            &:hover {
                background-color: #ff3300;
            }
        }
    }

}
</style>



